.todos{
    display: flex;
    flex: none;
    height:100%;
    width: 100%;
    justify-content: center;
    margin-right:400px;
    margin-top:30px;
}

.list{
    display:flex;
    flex-direction: column;
}
.todo-item{
    border:none;
    outline:none;
    width: 230px;
    height:5vh;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: left;
    box-shadow: -1px 2px 5px lightskyblue;
    padding:0 15px;
    margin-left: 35px;
    font-size:15px;
    background-color: transparent;
    color: white;
    border-radius: 50px;
    list-style: none;
}

.btn1{
    width:5vh;
    height:5vh;
    border:none;
    margin-left:10px;
    border-radius:50px;
    background: transparent;
    box-shadow: -1px 2px 5px lightskyblue;
    color:white;
    font-size: 19px;
    outline: none;
}

.btn2{
    width:5vh;
    height:5vh;
    border:none;
    margin-left:10px;
    border-radius:50px;
    background: transparent;
    box-shadow: -1px 2px 5px lightskyblue;
    color:white;
    font-size: 19px;
    outline: none;
}

.completed{
    opacity: .5;
    color:black;
    background-color: rgb(0, 72, 105);
}